
 import {
     Main,
     Home,
     Check,
     WorkPlan,
     Customer,
     College,
     Assessment,
     Assistant,
     Point,
     Login
 } from "@/views/index";
 import List from "../../views/list/list"
 import assessmentRouter from "../children/assessmentRouter";
 import assistantRouter from "../children/assistantRouter";
 import checkRouter from "../children/checkRouter";
 import controlRouter from "../children/controlRouter";
 import customerRouter from "../children/customerRouter";
 import pointRouter from "../children/pointRouter";
 import Message from "../../views/message/index"
const routers = [
 
    {
      path:"/",
      redirect:"/message"
    },
    {
      path:"/message",
      component:Message
    },
    {
      path:"/list",
      component:List
    },
    {
       path:"/main",
       component:Main,
       children:[
           {
               path:"/main/home",
               component:Home
           },
           {
               path:"/main/check",
               component:Check
           },
           {
            path:"/mian/workPlan",
            component:WorkPlan
          },
          {
            path:"/main/customer",
            component:Customer
          },
          {
            path:"/mian/college",
            component:College
          },
          {
            path:"/main/assessment",
            component:Assessment
          },
          {
            path:"/mian/assistant",
            component:Assistant
          },
          {
            path:"/main/point",
            component:Point
          },
          {
            path:"/main/login",
            component:Login
          },
          // {
          //   path:"/main",
          //   redirect:"/main/login"
          // },
         
          ...assessmentRouter,
          ...assistantRouter,
          ...checkRouter,
          ...controlRouter,
          ...customerRouter,
          ...pointRouter
       ]
    }
]
export default routers;