import React, { Component } from 'react'
import {Button,Icon} from "antd";
import {connect} from "react-redux"
class Cont extends Component {
    render() {
        console.log(this.props.add)
        return (
            <div>
                <Button onClick={()=>{
                    this.props.dispatch({type:"changeCont",flag:true})
                }}>
                    {
                        this.props.add ? <Icon type="loading"></Icon> :'+'
                    }
                </Button>
                <Button>{this.props.num}</Button>
                <Button onClick={()=>{
                    this.props.dispatch({type:"changeCont",flag:false})
                }}>{
                    this.props.dis ? <Icon type="loading"></Icon> :'-'
                }</Button>
            </div>
        )
    }
}
export default connect(state=>{
    console.log(state)
   return {...state.num,...state.loading}
})(Cont)
