const loaging = (state={
    add:false,
    dis:false
},action)=>{
    let newState = JSON.parse(JSON.stringify(state));
    switch(action.type){
        case "ADD_AA":
            {
            newState.add = !newState.add
             return newState
            }
        case "ADD_BB":
            {
            newState.dis = !newState.dis
            return newState
            }
        default :
            return state
    }
}
export default loaging;