const num = (state={num:0},action)=>{
     let newState = JSON.parse(JSON.stringify(state));
     console.log(newState,"pp");
     console.log(action)
     switch(action.type){
          case "ADD" :
               {
               newState.num = newState.num+action.nums
               return newState;
               }
          case "DIS" :
               {
               newState.num = newState.num-action.nums
               return newState;
               }
          default :
          return state
     }
}
export default num;