import {take,put,call,takeEvery} from "redux-saga/effects";
import axios from "axios";
const changeCont = function* (flag){
     console.log(flag);
     if(flag){
         yield put({type:"ADD_AA"})
        let data = yield call(axios.get,"/add");
        yield put({type:"ADD_AA"})
        yield put({type:"ADD",nums:data.data.num})
       
     }else{
         yield put({type:"ADD_BB"})
        let data = yield call(axios.get,"/dis");
        yield put({type:"ADD_BB"})
        yield put({type:"DIS",nums:data.data.num})
     }
}
export const cont = function* (){
      while(true){
       let action =  yield take("changeCont");
       yield changeCont(action.flag)
      }
}
